import React, { useState, useEffect } from "react";
import {
  makeStyles,
  GridList,
  Box,
  Grid,
  CircularProgress,
  GridListTile,
  useMediaQuery,
  useTheme
} from "@material-ui/core";
import axios from "axios";

const useStyles = makeStyles({
  root: {
    backgroundColor: "red"
  },
  gridTile: {
    height: "300px"
  }
});
const HomePage = () => {
  const classes = useStyles();
  const [data, setData] = useState({
    trendingList: [],
    loading: true,
    loadingImageList: []
  });
  const matchesMD = useMediaQuery(useTheme().breakpoints.up("md"));

  useEffect(() => {
    trendingList();
  }, []);

  const trendingList = () => {
    axios
      .get("http://api.giphy.com/v1/gifs/trending", {
        params: {
          api_key: "WYuuWO9uW1KJpPALeDKJM0NdJkjnccax"
        }
      })
      .then(res => {
        setData({ ...data, loading: false, trendingList: res.data.data });
      })
      .catch(err => {
        setData({ ...data, loading: false });
        console.log(err);
      });
  };

  const loadedImage = id => {
    setData({ ...data, loadingImageList: data.loadingImageList.concat(id) });
  };

  if (data.loading)
    return (
      <Box
        height="90vh"
        width="100%"
        display="flex"
        justifyContent="center"
        alignItems="center"
      >
        <CircularProgress size={80} />
      </Box>
    );

  return (
    <Box width="90%" m="50px auto">
      <Grid container spacing={1} wrap="wrap">
        {data.trendingList.map((item, index) => {
          return (
            <Grid
              key={item.id}
              item
              xs={12}
              md={index % 3 === 0 ? 6 : 3}
              lg={index % 3 === 0 ? 4 : 2}
              className={classes.gridTile}
            >
              <img
                height="100%"
                width="100%"
                onLoad={() => loadedImage(item.id)}
                src={item.images.downsized_large.url}
                style={
                  data.loadingImageList.includes(item.id)
                    ? { objectFit: "cover" }
                    : { display: "none" }
                }
                alt={item.title}
              />
              {data.loadingImageList.includes(item.id) ? null : (
                <Box
                  height="100%"
                  width="100%"
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                  style={{ backgroundColor: "#212121" }}
                >
                  <CircularProgress />
                </Box>
              )}
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
};

export default HomePage;
